<template>
  <img class="imgCard" :src="sourceImg"/>
</template>

<style scoped lang="css">
.imgCard {
  height: 80px;
  width: 80px;
}
</style>
<script>
export default {
  name: "PhotoCard",
  props: {
    src: String,
    origine: String
  },
  beforeMount() {
    this.listD = this.list;
  },
  data() {
    return {
      sourceImg: this.origine
        ? "/faces/" + this.origine + "/" + this.src
        : "/archives/" + this.src
    };
  }
};
</script>




