<template>
  <div :class="classes"  @click="onClick">
    
      <div class="col-md-12 align-center" >
        <img v-if="name!='inconnu'" src="../assets/Face.png" class="imgPeopleCard"/>
        <img v-else src="../assets/point-interrogation.png" class="imgPeopleCard"/>
        <div class="col-md-12 name ">{{name}}</div>
        <draggable v-model="listD"  @add="onAdd" class="dragArea" :options="options">
        
        </draggable></div>

   </div>
</template>

<style scoped lang="css">
.dragArea {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
}
.name {
  font-size: 15px;
  display: block;
  font-family: "Lucida Console";
}

.card {
  padding: 25px;
  margin: 25px;
  height: 150px;
  width: 150px;
}
.ghostClass {
  display: none;
}
.imgPeopleCard {
  height: 80px;
  width: 80px;
}
.peopleCard {
  display: block;
  height: 100px;
  width: 100px;
}

.color-0 {
  background-color: cadetblue;
}
.color-1 {
  background-color: red;
}

.color-2 {
  background-color: green;
}

.color-3 {
  background-color: blue;
}

.color-inconnu {
  background-color: lightgray;
}
</style>
<script>
import draggable from "vuedraggable";
export default {
  components: {
    draggable
  },
  name: "PeopleCard",
  props: {
    name: String,
    list: Array,
    index: Number
  },
  methods: {
    onClick(evt) {
      this.$emit("click", this.name);
    },
    onAdd(evt) {
      console.log(evt);
      this.list.push(this.listD[evt.newIndex]); // same properties as onEnd
    }
  },
  beforeMount() {
    this.listD = this.list;
  },
  data() {
    return {
      listD: [],
      options: {
        group: { name: "people", pull: false, put: true },
        ghostClass: "ghostClass",
        onMove(/**Event*/ evt) {
          console.log(evt); // same properties as onEnd
        }
      }
    };
  },
  computed: {
    classes() {
      return (
        "col-md-2 card color-" +
        (this.name === "inconnu" ? this.name : this.index)
      );
    },
    image() {
      return "../../../public/faces/" + this.name + "/" + list[0].name;
    }
  }
};
</script>




