<template>
  <div class="container">
   <div class="row">
<button type="button" @click="trainButton" class="btn btn-primary btn-lg">Entrainnement</button>
<button type="button" @click="saveButton" class="btn btn-primary btn-lg">Save</button>

   </div>
    <i class="fb-send" @click="trainButton">train</i>
    <div class="row" >
      <people-card  @click="selectPeople" v-for="element in peoples" v-bind:key="element" :name="element" :list="list[element]" :index="arrayPeople.indexOf(element)"/>
    </div>
   <div class="row" >
     
      <h2>Photos de {{selected}}</h2>
      <draggable v-model="list[selected]" class="col-md-12" :options="options">
        <photo-card v-for="file in list[selected]" :src="file.name" :origine="file.origine" v-bind:key="file.name"/>
      </draggable>
    </div>
  </div>
</template>


<style lang="css">
.imgCard {
  height: 150px;
  width: 150px;
}
.dragArea {
  min-height: 10px;
}
.ghostClass {
  display: none;
}
.people {
  float: left;
}
</style>
<script>
import axios from "axios";
import PeopleCard from "./PeopleCard";
import PhotoCard from "./PhotoCard";
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
    PeopleCard,
    PhotoCard
  },
  name: "HelloWorld",
  props: {
    msg: String
  },

  data() {
    return {
      list: {
        toto: [{ name: "eee" }],
        tata: [],
        titi: [{ name: "eee" }],
        inconnu: [{ name: "eee" }]
      },
      arrayPeople: ["toto", "tata", "titi", "inconnu"],
      selected: "inconnu",
      options: {
        group: { name: "people", pull: true, put: false },
        ghostClass: "ghostClass"
      }
    };
  },
  sockets: {
    connect() {
      console.log("socket connected");
    },
    customEmit(val) {
      console.log(
        'this method was fired by the socket server. eg: io.emit("customEmit", data)'
      );
    }
  },
  computed: {
    peoples() {
      return Object.keys(this.list).filter(people => people !== this.selected);
    }
  },
  methods: {
    selectPeople(val) {
      this.selected = val;
      console.log(this.list[val]);
    },
    trainButton(val) {
      // $socket is socket.io-client instance
      this.$socket.emit("train", val);
    },
    saveButton(val) {
      // $socket is socket.io-client instance
      this.$socket.emit("blink", val);
    }
  },
  mounted() {
    axios
      .get(`/train/faces`)
      .then(response => {
        // JSON responses are automatically parsed.
        this.list = response.data;
        this.arrayPeople = [];

        Object.keys(this.list).forEach(key => this.arrayPeople.push(key));
        console.log(this.arrayPeople);
      })
      .catch(e => {
        console.log("e", e);
        // this.errors.push(e)
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



